import { IBaseMsg } from "../IMsg";


export interface IUsageMetrics extends IBaseMsg {
    type: "studioAIService.info.usageMetrics",
    functionName: string,
    data: {
        id: string,
        model: string,
        object: string,
        usage: {
            completion_tokens: number,
            prompt_tokens: number,
            total_tokens: number
        }
    }
    statusCode: number,
    statusText: string
}

export interface IUsageMetricsCreateUI<GraphT = Record<string, any>, UsageT = Record<string, any>> extends IBaseMsg {
    type: "studioAIService.createUI.usageMetrics";
    graphData: GraphT;
    usage: UsageT;
}


